//libs
import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import moment from 'moment';
import { DayPicker } from 'react-day-picker';

//components
import CustomTextInput from 'components/CustomTextInput';
import CheckClickOutside from 'components/OutsideClick';
import CustomButton from 'components/CustomButton';
import ListingModal from 'components/ListingModal';

//consts
import { Images } from 'shared/Images';
import { INPUT_TYPES, LABEL, PLACEHOLDERS, STRINGS } from 'shared/Constants';
import { onCheckboxSelection, splice} from 'shared/Utilities';

//styles
import './style.scss';


const VacanciesDetails = ({
  data = {},
  changeHandler = () => {},
  errorMessages = {},
  disabledInputs = {},
  isAdd = false,
  isEdit = false,
  branches,
  services
}) => {
  const dispatch = useDispatch()
  const [showPicker, setShowPicker] = useState(false);

  //Service States
  const [showServices, setShowServices] = useState(false)
  const [displayServices, setDisplayServices] = useState([]);
  const [selectedServices, setSelectedServices] = useState([]);
  const [isAllSelected, setisAllSelected] = useState(false);
  const [keyword, setKeyword] = useState(null);
  const isMulti = true;

  //Branch States
  const [showBranches, setShowBranches] = useState(false);
  const [displayBranches, setDisplayBranches] = useState([])
  const [selectedBranches, setSelectedBranches] = useState({})
  const [branchKeyWord, setBranchKeyword] = useState(null)

  const handleIconClick = () => {
    setShowPicker((prevState) => !prevState);
  };
  console.log(showPicker, "branchesbranches")
  useEffect(() => {
    setDisplayBranches(branches);
    setDisplayServices(services);
  }, [services, branches])
  
  //For Services

  //For updating main state on change of services
  // useEffect(() => {
  //   changeHandler(selectedServices, "vacancy_services")
  // }, [selectedServices])

  //For updating main state on change of services
  // useEffect(() => {
  //   changeHandler(selectedBranches, "branch_id");
  //   if(isAdd){
  //     manageSelectedServicesOfBranch()
  //   }
  // }, [selectedBranches])

  //For setting initial checkbox to true
  useEffect(() => {
    if(isEdit && services && branches){
      setDisplayServices(prevState => {
        let newData = [];
        prevState.forEach(item => {
          if(data.vacancy_services.find(service => service.id == item.id)){
            newData.push({...item, value: true})
          }else{
            newData.push(item)
          }
        })
        return newData;
      })
      setSelectedServices([...data.vacancy_services])

      setDisplayBranches(prevState => {
        console.log(prevState, data.branch_id, "tadfadsfadsgfvdf")
        let newData = [];
        prevState?.forEach(item => {
          if(data.branch_id.id == item.id){
            newData.push({...item, value: true})
          }else{
            newData.push(item)
          }
        })
        return newData
      })
      setSelectedBranches(data.branch_id)
    }
  }, [data.vacancy_services, data.branch_id, services, branches])

  const manageSelectedServicesOfBranch = (selectedBranches) => {
    if(Object.keys(selectedBranches)?.length){
      let branchSelectedServices = services.map(item => {
        if(selectedBranches?.services.find(service => service.id == item.id)){
          return {
            ...item,
            value: true
          }
        }else{
          return item
        }
      })
      setDisplayServices(branchSelectedServices)
      setSelectedServices(branchSelectedServices.filter(item => item.value))
      changeHandler(branchSelectedServices.filter(item => item.value), "vacancy_services")
    }
  }

  const handleCheckBoxSelection = (e, value) => {
      let newState = []
      if (isMulti) {
          onCheckboxSelection(e, setDisplayServices);
          if (e.target.checked) {
            newState = [...selectedServices, value]
            setSelectedServices(newState);
            if (selectedServices?.length + 1 === services?.length) {
                setisAllSelected(true);
            }
          } else {
            // setSelectedServices(splice(selectedServices, value?.id));
            newState = [...selectedServices]
            newState.filter(item => item.id != value.id);
            setSelectedServices(newState);
            setisAllSelected(false);
          }
      } else {
          onCheckboxSelection(e, setDisplayServices, true);
          if (e.target.checked) {
              newState = [...selectedServices, value]
              setSelectedServices(newState);
          } else {
              setSelectedServices(newState);
          }
      }

      //For updating main state on change of services
      changeHandler(newState, "vacancy_services")
  };

  const handleAllSelected = (e, value) => {
      let newState = []
      if (e.target.checked) {
          newState = services.map((value, index) => value)
          setSelectedServices(newState);
          setisAllSelected(true);
      } else if (selectedServices?.length > services.length) {
          setisAllSelected(false);
      } else {
          setSelectedServices(newState);
          setisAllSelected(false);
      }

      //For updating main state on change of services
      changeHandler(newState, "vacancy_services")

      services.map((value, index) => {
        setDisplayServices((prevState) => {
              let copyPrevState = [...prevState];
              let index = copyPrevState.findIndex((item) => item.id == value.id);
              copyPrevState[index].value = e.target.checked;
              return copyPrevState;
          });
      });
  };

  const handleChangeKeyword = (e) => {
      setKeyword(e.target.value);
  };

  const handleSearchData = (e) => {
      let key = keyword.toLowerCase();

      // Use filter() method to create a new array with matching objects
      if (key?.length) {
          const searchResults = services.filter((service) => {
              // Extract the values from the object and convert them to lowercase
              const name = service.title.toLowerCase();

              // Check if the keyword is present in the name or category
              return name.includes(key);
          });
          // Return the search results
          setDisplayServices(searchResults);
      } 
      // else {
      //   manageSelectedServicesOfBranch()
      // }
  };


  //For Branch
  const handleBranchSelection = (e, value) => {
    onCheckboxSelection(e, setDisplayBranches, true);
    console.log(value, "valuevalues")
    if (e.target.checked) {
      setSelectedBranches(value);
      changeHandler(value, "branch_id");
      manageSelectedServicesOfBranch(value)
    } else {
      setSelectedBranches({});
      changeHandler({}, "branch_id");
      // manageSelectedServicesOfBranch()
    }
  };

  const handleBranchChangeKeyword = (e) => {
    setBranchKeyword(e.target.value);
  };

  const handleBranchSearchData = (e) => {
    let key = branchKeyWord.toLowerCase();

    // Use filter() method to create a new array with matching objects
    if (key?.length) {
        const searchResults = branches.filter((service) => {
            // Extract the values from the object and convert them to lowercase
            const name = service.title.toLowerCase();

            // Check if the keyword is present in the name or category
            return name.includes(key);
        });
        // Return the search results
        setDisplayBranches(searchResults);
    } else {
      setDisplayBranches(branches);
    }
  };

  const handleRemoveSelectedService = (service) => {
    let newState = selectedServices.filter(item => item.id !== service.id)
    setSelectedServices(newState)
    changeHandler(newState, "vacancy_services")
    setDisplayServices(prevState => prevState.map(item => {
      if(item.id === service.id){
        return {
          ...item,
          value: false
        }
      }else{
        return item;
      }
    }))
  }

  return (
    <div className="admin_view field_white">
      <div className="row mt-3 ">
        <div className="col-md-4">
          <CustomTextInput
            type={INPUT_TYPES.TEXT}
            titleText={LABEL.PHARMACY_NAME}
            disabled={disabledInputs['pharmacy_name']}
            value={data['pharmacy_name']}
          />
        </div>

        <div className="col-md-4">
          <CustomTextInput
            type={INPUT_TYPES.TEXT}
            titleText={LABEL.JOB_NAME}
            disabled={disabledInputs['job_name']}
            value={data['job_name']}
            placeholder={PLACEHOLDERS.ENTER_HERE}
            onChange={(e) => changeHandler(e.target.value, 'job_name')}
            errorMessage={errorMessages['job_name']}
          />
        </div>

        <div className="col-md-4">
          <CheckClickOutside
            onClick={() => {
              setShowPicker(false);
            }}
          >
            <CustomTextInput
              type={INPUT_TYPES.TEXT}
              titleText={LABEL.START_DATE}
              disabled={disabledInputs['selectedDay']}
              value={
                data['selectedDay'].from && data['selectedDay'].to
                  ? `${moment(data['selectedDay'].from).format(
                      'DD-MM-YYYY'
                    )} to ${moment(data['selectedDay'].to).format(
                      'DD-MM-YYYY'
                    )}`
                  : ''
              }
              placeholder={PLACEHOLDERS.SELECT}
              onIconClick={handleIconClick}
              icon={Images.calendar}
              errorMessage={errorMessages['selectedDay']}
            />
            {showPicker ? (
              <DayPicker
                mode="range"
                onSelect={(day) => {
                  if (day) {
                    if (day.from && day.to) {
                      setShowPicker(false);
                    }
                    changeHandler(day, 'selectedDay');
                    // setSelectedDays(day);
                  }
                }}
                disabled={[
                  {
                    from: new Date(1970),
                    to: new Date(
                      moment().subtract(1, 'days').format('YYYY, MM, DD')
                    ),
                  },
                ]}
                selected={data['selectedDay']}
              />
            ) : (
              ''
            )}
          </CheckClickOutside>
        </div>

        <div className="d-flex align-items-end">
          <div className="col-md-2">
            <CustomTextInput
              type={INPUT_TYPES.NUMBER}
              titleText={LABEL.RATE}
              disabled={disabledInputs['start_rate']}
              value={data['start_rate']}
              onChange={(e) => changeHandler(e.target.value, 'start_rate')}
              errorMessage={errorMessages['start_rate']}
            />
          </div>
          <span className="p-3"> to </span>
          <div className="col-md-2">
            <CustomTextInput
              type={INPUT_TYPES.NUMBER}
              disabled={disabledInputs['end_rate']}
              value={data['end_rate']}
              onChange={(e) => changeHandler(e.target.value, 'end_rate')}
              errorMessage={errorMessages['end_rate']}
            />
          </div>
          <div className="col-md-4">
            <CustomTextInput
              type={INPUT_TYPES.TEXT}
              titleText={LABEL.BRANCH}
              disabled={disabledInputs['branch_id']}
              value={data['branch_id'].title}
              onChange={(e) => changeHandler(e.target.value, 'branch_id')}
              errorMessage={errorMessages['branch_id']}
              icon={Images.ArrowDown}
              onIconClick={() => setShowBranches(true)}
            />
            <ListingModal
              data={displayBranches}
              setShow={setShowBranches}
              show={showBranches}
              handleSelection={handleBranchSelection}
              heading={"Select Branch: "}
              multipleSelection={false}
              buttonType={INPUT_TYPES.RADIO}
              keyword={branchKeyWord}
              handleChangeKeyword={handleBranchChangeKeyword}
              handleSearchData={handleBranchSearchData}
            />
          </div>
        </div>
      </div>
      <div className="row">
         <div className="col-md-10 d-flex service_listing">
          {data['vacancy_services'].map(item => {
            return <div className='service_item'>
              <span>{item.title}</span>
              <em> <img width="12" src={Images.deleteCvIcon} alt="" onClick={() => handleRemoveSelectedService(item)}/></em>
            </div>
          })}
          {errorMessages['vacancy_services'] ? <div className="error">{errorMessages['vacancy_services']}</div> : null}
         </div>
         <div className="col-md-2">
         <CustomButton
            className="btn btn-md btn-primary"
            title={STRINGS.SELECT_SERVICES}
            onClick={() => setShowServices(true)}
          />
         </div>
         <ListingModal
            data={displayServices}
            setShow={setShowServices}
            show={showServices}
            handleSelection={handleCheckBoxSelection}
            selectedData={selectedServices}
            handleSelectAll={handleAllSelected}
            isAllSelect={isAllSelected}
            heading={"Select Services that user can access: "}
            multipleSelection={isMulti}
            buttonType={!isMulti ? INPUT_TYPES.RADIO : INPUT_TYPES.CHECKBOX}
            keyword={keyword}
            handleChangeKeyword={handleChangeKeyword}
            handleSearchData={handleSearchData}
        />
      </div>
      <div className="row">
        <CustomTextInput
          type={INPUT_TYPES.TEXT_AREA}
          titleText={LABEL.DESCRIPITON}
          disabled={disabledInputs['description']}
          value={data['description']}
          onChange={(e) => changeHandler(e.target.value, 'description')}
          errorMessage={errorMessages['description']}
        />
      </div>
    </div>
  );
};

export default VacanciesDetails;
