import React from 'react'
// import './style.scss'

const ToggleButton = ({checked, handleToggleChange, labelText}) => {
  return (
    <div className="toggle-button">
        <input
        type="checkbox"
        id="switch"
        checked={checked}
        onChange={handleToggleChange}
        />
        <label htmlFor="switch">{labelText}</label>
    </div>

  )
}

export default ToggleButton