import React, { useEffect, useState } from 'react';
import TimePicker from 'rc-time-picker';
import moment from 'moment';
import Accordion from 'react-bootstrap/Accordion';

//constants
import {
  INPUT_TYPES,
  LABEL,
  STRINGS,
  days,
  initialDaysData,
} from 'shared/Constants';

//Utilities
import { appendSeconds } from 'shared/Utilities';

//styles
import 'bootstrap/dist/css/bootstrap.min.css';
import './style.scss';
import ToggleButton from 'components/ToggleButton';
import CustomButton from 'components/CustomButton';

let labels = {
  opening_time: 'Opening Time',
  closing_time: 'Closing Time',
  lunch_start: 'Lunch Start',
  lunch_end: 'Lunch End',
};

const JobTiming = ({
  jobTimingData = [],
  setJobTimingData = () => {},
  disabled = false,
  handleDisabledHours = () => {},
  handleDisabledMinutes = () => {},
  closedDays = []
}) => {
  console.log("reached eherdewr", jobTimingData)
  const [selectedDay, setSelectedDay] = useState(jobTimingData?.[0]?.day);

  const handleAccordionChange = (key) => {
    setSelectedDay(key);
  };

  const handleTimeChange = (event, field, day) => {
    setJobTimingData((prevJobTimingData) => {
      const updatedData = prevJobTimingData.map((data) => {
        if (data.day === day) {
          return {
            ...data,
            [field]: moment(event).format('HH:mm:ss'),
          };
        }
        return data;
      });
      return updatedData;
    });
  };

  const onCheckboxSelect = (e, index, item) => {
    console.log(closedDays, item.day, "dagtaadfadsf")
    if(closedDays.length){  
      if(closedDays.includes(item.day)){
        return;
      }
    }
    let newState = [...jobTimingData];
    if (!e.target.checked) {
      newState.splice(index, 1, {
        ...newState[index],
        closing_time: '',
        opening_time: '',
        lunch_start: '',
        lunch_end: '',
        isSelected: false,
      });
    } else {
      newState.splice(index, 1, {
        ...newState[index],
        closing_time: '',
        opening_time: '',
        lunch_start: '',
        lunch_end: '',
        isSelected: true,
      });
    }
    setJobTimingData([...newState]);
  };

  const isDisabled = (field) => {
    let timings = jobTimingData.find((item) => item.day == selectedDay);
    if (!timings?.isSelected) {
      return true;
    }
    if (field == 'closing_time' || field == 'opening_time') {
      if (field === 'closing_time' && !timings['opening_time']) {
        return true;
      } else if (field === 'closing_time' && timings['opening_time']) {
        return false;
      }
    }
    if (field == 'lunch_end' || field == 'lunch_start') {
      if (!timings['opening_time'] || !timings['closing_time']) {
        return true;
      }
      if (field === 'lunch_end' && !timings['lunch_start']) {
        return true;
      } else if (field === 'lunch_end' && timings['lunch_start']) {
        return false;
      }
    }
  };

  const handleSameForAllWeek = (index) => {
    let selectedData = jobTimingData[index]
    setJobTimingData((prevJobTimingData) => {
      const updatedData = prevJobTimingData.map((data) => {
        if (data.isSelected) {
          return {
            ...data,
            closing_time: selectedData.closing_time,
            opening_time: selectedData.opening_time,
            lunch_start: selectedData.lunch_start,
            lunch_end: selectedData.lunch_end,
          };
        }
        return data;
      });
      return updatedData;
    });
  };

  return (
    <>
      <div className="mb-2">
        <h6 className="label_title mb-4">
          <span>{LABEL.JOB_TIMINGS}</span>
        </h6>
      </div>
      <Accordion activeKey={selectedDay} onSelect={handleAccordionChange}>
        {jobTimingData.map((item, idx) => (
          <Accordion.Item eventKey={item.day}>
            <Accordion.Header>
              <ToggleButton
                checked={item.isSelected}
                handleToggleChange={(e) => onCheckboxSelect(e, idx, item)}
                labelText={days[item.day]}
              />
              {selectedDay == item.day ? (
                <CustomButton
                  className="btn btn-md btn-primary"
                  title={STRINGS.SAME_FOR_WEEK}
                  onClick={() => handleSameForAllWeek(idx)}
                  disabled={item.isSelected ? false : true}
                />
              ) : item.isSelected ? (
                <div>
                  Time:{' '}
                  {item['opening_time']
                    ? moment(item['opening_time'], 'hh:mm:ss').format('hh:mm A')
                    : 'N/A'}{' '}
                  -{' '}
                  {item['closing_time']
                    ? moment(item['closing_time'], 'hh:mm:ss').format('hh:mm A')
                    : 'N/A'}{' '}
                  Lunch:{' '}
                  {item['lunch_start']
                    ? moment(item['lunch_start'], 'hh:mm:ss').format('hh:mm A')
                    : 'N/A'}{' '}
                  -{' '}
                  {item['lunch_end']
                    ? moment(item['lunch_end'], 'hh:mm:ss').format('hh:mm A')
                    : 'N/A'}
                </div>
              ) : (
                <span>Closed</span>
              )}
            </Accordion.Header>
            <Accordion.Body>
              <div className="card mb-4">
                <div className="card-body bg-white">
                  <div className="row">
                    {Object.keys(labels).map((field, index) => (
                      <div className="col-md-3 my-2" key={index}>
                        <label htmlFor={field}>{`${labels[field]}`}</label>
                        <TimePicker
                          use12Hours={true}
                          showSecond={false}
                          value={
                            item[field] ? moment(item[field], 'HH:mm:ss') : ''
                          }
                          onChange={(event) =>
                            handleTimeChange(event, field, item.day)
                          }
                          disabledHours={() =>
                            handleDisabledHours(field, selectedDay)
                          }
                          disabledMinutes={() =>
                            handleDisabledMinutes(field, selectedDay)
                          }
                          disabled={disabled || isDisabled(field)}
                          allowEmpty={false}
                        />
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </Accordion.Body>
          </Accordion.Item>
        ))}
      </Accordion>
    </>
  );
};

export default JobTiming;
