//libs
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import { useDispatch, useSelector } from 'react-redux';
import moment from 'moment';

//components
import JobTiming from 'components/JobTimings';
import BreadCrumb from 'components/BreadCrumb';
import VacanciesDetails from '../components/vacanciesDetails';
import CustomButton from 'components/CustomButton';

//Utilities
import { createVacancyPayload, makeJobDaysListing, structureBranchData, structureData } from 'shared/Utilities';

//style
import 'react-day-picker/dist/style.css';

//consts
import { ADMIN_ROUTES, BREAD_CRUMB_TEXT, ERROR_MESSAGES, STRINGS, days } from 'shared/Constants';

//actions
import { getBranchListing } from 'redux/Actions/PharmacyAdminDashboard';
import { getServices } from 'redux/Actions/PharmacyAdminProfile';
import { updateVacancy } from 'redux/Actions/PharmacyAdminDashboard';

const CreateVacancy = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const profile = useSelector(state => state.common.profileData)
  const [daysError, setDaysError] = useState('');
  const [jobTimingData, setJobTimingData] = useState([]);
  const [vacancyData, setVacancyData] = useState({
    pharmacy_name: profile?.pharmacy_info?.pharmacy_name,
    job_name: "",
    selectedDay: {
      from: "",
      to: ""
    },
    description: "",
    start_rate: "",
    end_rate: "",
    branch_id: "",
    vacancy_services: [],
  });
  const [vacanyErrors, setVacancyErrors] = useState({
    pharmacy_name: "",
    job_name: "",
    selectedDay: "",
    description: "",
    start_rate: "",
    end_rate: "",
    branch_id: "",
  })
  const [branches, setBranches] = useState([])
  const [services, setServices] = useState([])
  const [closedBranchDays, setClosedBranchDays] = useState([])
  useEffect(() => {
    if(vacancyData?.['branch_id']?.job_timings){
      let closedDays = []
      Object.keys(days).forEach(day => {
        if(!vacancyData?.['branch_id']?.job_timings?.find(item => item.day == day)){
          closedDays.push(Number(day))
        }
      })
      setClosedBranchDays(closedDays)
    }
  }, [vacancyData['branch_id']])

  useEffect(() => {
    fetchServiceListCall()
    fetchBranchListingCall()
  }, []);

  useEffect(() => {
    if ((!vacancyData?.selectedDay?.from || !vacancyData?.selectedDay?.to) && !Object.keys(vacancyData['branch_id']).length) return;
    if (moment(vacancyData?.selectedDay?.to).diff(vacancyData?.selectedDay?.from, 'days') > 6) {
      setDaysError(ERROR_MESSAGES.SELECT_7_DAYS);
    } else {
      setDaysError('');
      let selectedDaysData = makeJobDaysListing(vacancyData?.selectedDay?.from,vacancyData?.selectedDay?.to)
      vacancyData?.['branch_id']?.job_timings?.forEach(item => {
          let index = selectedDaysData.findIndex(day => item.day == day.day)
          selectedDaysData.splice(index, 1, {...selectedDaysData[index], ...item, isSelected: true})
      });
      setJobTimingData(selectedDaysData)
    }
  }, [vacancyData?.selectedDay?.from, vacancyData?.selectedDay?.to, vacancyData['branch_id']]);

  const handleBreadCrumbClick = () => {
    history.push(ADMIN_ROUTES.VACANCIES)
  }

  const handleChangeError = (field, message) => {
    let errorState = {...vacanyErrors};
    errorState[field] = message;
    setVacancyErrors(errorState)
  }

  const changeHandler = (value, field) => {
    handleChangeError(field, value ? "" : ERROR_MESSAGES.GENERIC_ERROR)
    setVacancyData(prevState => {
      return {...prevState, [field] : value}
    })
  }

  const fetchBranchListingCall = () => {
    let payload = {
        isJobTimingsRequired: 1
    };
    dispatch(
        getBranchListing(
            payload,
            (res = {}) => {
                setBranches(structureBranchData(res.data, "branch_name"));
            },
        ),
    );
  };

  const fetchServiceListCall = () => {
    dispatch(
      getServices((response) => {
          setServices(structureData(response.data, "service_name"));
      }),
    );
  }

  const handleDisabledHours = (field, day) => {
    if(!jobTimingData?.length){
        return []
    }
    let initialTiming = vacancyData?.['branch_id']?.job_timings?.find(item => item.day == day);
    let branchOpeningHour = initialTiming['opening_time'].split(":")[0]; 
    let branchClosingHour = initialTiming['closing_time'].split(":")[0]; 
    let jobTimingDay = jobTimingData.find(item => item.day ==  day)
    let finalArr = []
    if(field === 'opening_time'){
       for(let i=0; i<= branchOpeningHour; i++){
        finalArr.push(i)
       }
    }
    if(field === 'closing_time'){
       let hour = jobTimingDay['opening_time'].split(":")[0];
       for(let i=0; i<= 24; i++){
        if(i <= hour || i > branchClosingHour){
          finalArr.push(i)
        }
       }
    }
    if(field === 'lunch_start'){
      let hourStart = jobTimingDay['opening_time'].split(":")[0];
      let hourEnd = jobTimingDay['closing_time'].split(":")[0];
      for(let i=0; i<= 24; i++){
          if(i<= hourStart || i>= hourEnd){
              finalArr.push(i)
          }
      }
    }
    if(field === 'lunch_end'){
      let hourStart = jobTimingDay['lunch_start'].split(":")[0];
      let hourEnd = jobTimingDay['closing_time'].split(":")[0];
      for(let i=0; i<= 24; i++){
          if(i< hourStart || i>= hourEnd){
              finalArr.push(i)
          }
      }
    }
    return finalArr;
  }

  const handleDisabledMinutes = (field, day) => {
    if(!jobTimingData?.length){
        return []
    }
    let jobTimingDay = jobTimingData.find(item => item.day ==  day)
    let finalArr = []
    if(field === 'lunch_end'){
        let hourLunchStart = jobTimingDay['lunch_start'].split(":")[0];
        let minuteStart = jobTimingDay['lunch_start'].split(":")[1];
        let hourLunchEnd = jobTimingDay['lunch_end'].split(":")[0];
        if(hourLunchEnd == hourLunchStart){
            for(let i=0; i<= 60; i++){
                if(i<= minuteStart){
                    finalArr.push(i)
                }
            }
        }
    }
    return finalArr;
  }

  const createVacancyPostRequest = () => {
    let error = {      
        pharmacy_name: "",
        job_name: !vacancyData.job_name ? ERROR_MESSAGES.GENERIC_ERROR : "",
        selectedDay: !vacancyData.selectedDay.from || !vacancyData.selectedDay.to ? ERROR_MESSAGES.GENERIC_ERROR : "",
        start_rate: !vacancyData.start_rate ? ERROR_MESSAGES.GENERIC_ERROR : "",
        end_rate: !vacancyData.end_rate ? ERROR_MESSAGES.GENERIC_ERROR : "",
        branch_id: !vacancyData.branch_id?.id ? ERROR_MESSAGES.GENERIC_ERROR : "", 
        vacancy_services: !vacancyData.vacancy_services?.length ? ERROR_MESSAGES.GENERIC_ERROR : "", 
    }
    setVacancyErrors(error);
    if(Object.values(error).find(item => item)){
      return;
    }
    let payload = createVacancyPayload(vacancyData, jobTimingData);
    dispatch(updateVacancy({data: payload}))
  }
  
  return (
    <>
      <BreadCrumb
          onClick={handleBreadCrumbClick}
          previousLink={BREAD_CRUMB_TEXT.VACANCIES}
          currentLink={BREAD_CRUMB_TEXT.ADD_NEW_VACANCY}
      />
      <VacanciesDetails
        data={vacancyData}
        changeHandler={changeHandler}
        errorMessages={vacanyErrors}
        isAdd={true}
        disabledInputs={
          {pharmacy_name: true}
        }
        branches={branches}
        services={services}
      />

      {vacancyData?.selectedDay.from && vacancyData?.selectedDay.to && Object.keys(vacancyData['branch_id']).length ? (
        <JobTiming jobTimingData={jobTimingData} setJobTimingData={setJobTimingData} closedDays={closedBranchDays} handleDisabledHours={handleDisabledHours} handleDisabledMinutes={handleDisabledMinutes}/>
      ) : null}

      <CustomButton
        className="btn btn-md btn-primary"
        title={STRINGS.SAVE}
        onClick={createVacancyPostRequest}
      />
    </>
  );
};

export default CreateVacancy;
