import CustomCheckBox from "components/CustomCheckBox";
import CustomModal from "components/CustomModal";
import SearchBar from "components/SearchBar";
import React from "react";
import { INPUT_TYPES } from "shared/Constants";

const ListingModal = ({
    data = [],
    onChange = () => {},
    setShow = () => {},
    show = false,
    selectedData = [],
    handleSearchData = () => {},
    handleSelection = () => {},
    handleChangeKeyword = () => {},
    keyword,
    checkbox = "false",
    multipleSelection = true,
    disabled = false,
    isAllSelect = false,
    handleSelectAll = () => {},
    heading = "Select option user can access",
    buttonType = INPUT_TYPES.CHECKBOX,
}) => {
    const checkBoxClass = checkbox ? "col-md-3 mt-2" : "col-md-3 mt-2";
    return (
        <CustomModal
            bodyClassname={""}
            isOpen={show}
            large="true"
            handleToggle={() => setShow((prev) => !prev)}
        >
            {multipleSelection ? (
                <div className="d-flex justify-content-lg-between my-4 p-3 mb-2 bg-note">
                    <label>{heading}</label>
                    <div className="checkbox">
                        <input
                            type={buttonType}
                            disabled={disabled}
                            checked={isAllSelect}
                            onChange={(e) => handleSelectAll(e)}
                        />
                        <label>Select All</label>
                    </div>
                </div>
            ) : null}
            <div className="modal-body pt-0">
                <div className="my-3">
                    <SearchBar
                        searchAPI={handleSearchData}
                        keyword={keyword}
                        handleChangeKeyword={handleChangeKeyword}
                    />
                </div>
                <div className="row m-1">
                    {data.length ? data.map((checkBoxItem, checkboxIndex) => (
                        <div className={checkBoxClass} key={checkboxIndex}>
                            <div key={checkboxIndex} className={`checkbox mb-3`}>
                                <input
                                    id={checkBoxItem.id}
                                    checked={checkBoxItem.value}
                                    type={buttonType}
                                    onChange={(e) => handleSelection(e, checkBoxItem)}
                                />
                                <label>{checkBoxItem.title}</label>
                            </div>
                        </div>
                    )) : <span className="text-center">No Data Found</span>}
                </div>
            </div>
        </CustomModal>
    );
};

export default ListingModal;
