import CustomModal from "components/CustomModal";
import React from "react";
import { useState } from "react";
import ApplyJobform from "./ApplyJobform";

function VacancyCard({ data = [], onCardClick = () => {}, onApply = () => {} }) {
    const [showModal, setShowModal] = useState(false);
    const ApplyJobModal = () => {
        return (
            <CustomModal
                isOpen={showModal}
                medium={true}
                handleToggle={() => setShowModal((prev) => !prev)}
            >
                <ApplyJobform />
            </CustomModal>
        );
    };

    return (
        <div className="d-flex flex-wrap">
            {(data.length ? data : [1, 2, 3, 4, 5, 6, 7, 8, 8]).map((i, index) => {
                return (
                    <div id={index} className="card m-3">
                        <div onClick={onCardClick}>
                            <h6 className="card-title">Pharmacist</h6>
                            <span>Tuesday, 21 Feb 2023</span>
                            <div className="card-body">
                                <h6>Sharief HC - Grove Lane - WA159JX</h6>
                                <table>
                                    <tr>
                                        <th scope="row">£25/hr</th>
                                        <td>09:00-18:00</td>
                                    </tr>
                                </table>
                            </div>
                        </div>
                        <button
                            className="btn btn-outline-secondary"
                            onClick={() => setShowModal(true)}
                        >
                            Apply
                        </button>
                    </div>
                );
            })}
            <ApplyJobModal />
        </div>
    );
}

export default VacancyCard;
