import HeroBanner from "components/HeroBanner";
import ReactSelect from "components/ReactSelect";
import React, { useState } from "react";
import { Images } from "shared/Images";
import { STRINGS } from "./constants";
import CustomTextInput from "components/CustomTextInput";
import Footer from "components/Footer/Footer";
import Register from "components/OurTeam";
import { useSelector } from "react-redux";
import Newsletter from "components/Newsletter";
import JoinTeam from "./JoinTeam";
import { useHistory } from "react-router-dom";
import { INPUT_TYPES, LABEL, PLACEHOLDERS, ROUTES } from "shared/Constants";
import VacancyCard from "components/VacancyCard";
import SearchBar from "components/SearchBar";
import { DayPicker } from "react-day-picker";
import moment from "moment";
import CheckClickOutside from "components/OutsideClick";

function VacancyListing() {
    const history = useHistory();
    const [location, setLocation] = useState("");
    const [dateRange, setDateRange] = useState("");

    const [showPicker, setShowPicker] = useState(false);
    const handleIconClick = () => {
        setShowPicker((prevState) => !prevState);
    };
    const aboutUs = useSelector((state) => state.dynamic.aboutUs);
    const options = [
        { value: true, label: "Activate", color: "green" },
        { value: false, label: "Deactivate", color: "red" },
    ];

    const selectedOption = options[0];

    const handleChange = (selectedOption) => {};
    const changeHandler = (value, field) => {
        setDateRange(value);
    };
    return (
        <div>
            <section className="hero_banner">
                <div className="hero_bg">
                    <img src={Images.HeroBg} alt="Locumbay" width="300" className="w-100" />
                </div>
                <div className="container">
                    <div className="row align-items-center banner_info">
                        <div className="banner_content">
                            <h1 className="h1">{STRINGS.VACANCY_HEAD}</h1>
                        </div>
                    </div>
                </div>
            </section>

            <div className="card">
                <div className="card-body bg-dark d-flex justify-content-evenly">
                    <div>
                        <label className="text-light">{STRINGS.SEARCH}</label>
                        <SearchBar />
                    </div>
                    <div>
                        <label className="text-light">{STRINGS.DATE}</label>
                        <div className="col-md-4">
                            <CheckClickOutside
                                onClick={() => {
                                    setShowPicker(false);
                                }}
                            >
                                <CustomTextInput
                                    type={INPUT_TYPES.TEXT}
                                    titleText={LABEL.START_DATE}
                                    value={""}
                                    placeholder={PLACEHOLDERS.SELECT}
                                    onIconClick={handleIconClick}
                                    icon={Images.calendar}
                                />
                                {showPicker ? (
                                    <DayPicker
                                        mode="range"
                                        onSelect={(day) => {
                                            if (day) {
                                                if (day.from && day.to) {
                                                    setShowPicker(false);
                                                }
                                                changeHandler(day, "selectedDay");
                                                // setSelectedDays(day);
                                            }
                                        }}
                                        disabled={[
                                            {
                                                from: new Date(1970),
                                                to: new Date(
                                                    moment()
                                                        .subtract(1, "days")
                                                        .format("YYYY, MM, DD"),
                                                ),
                                            },
                                        ]}
                                        selected={""}
                                    />
                                ) : (
                                    ""
                                )}
                            </CheckClickOutside>
                        </div>
                    </div>
                    <div>
                        <label className="text-light">{STRINGS.RATE}</label>
                        <CustomTextInput
                            value={location}
                            onChange={(e) => setLocation(e.target.value)}
                        />
                    </div>
                    <button className="btn btn-primary">{STRINGS.SEARCH}</button>
                </div>
            </div>

            <VacancyCard
                onCardClick={() => history.push(ROUTES.VIEW_VACANCY)}
                onApply={() => history.push(ROUTES.VIEW_VACANCY)}
            />
            <JoinTeam />

            <Footer />
        </div>
    );
}

export default VacancyListing;
